.features-section {
    padding: 50px 0;
    background-color: #f9f9f9;
}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.feature {
    text-align: center;
    margin-bottom: 40px;
}

.feature img {
    width: 50px;
    height: 50px;
    margin-bottom: 20px;
}

.feature h2 {
    font-size: 24px;
    color: #333;
    margin-bottom: 10px;
}

.feature p {
    font-size: 16px;
    color: #666;
}
